import React, {useCallback, useContext, useMemo, useState} from "react";
import {Grid, TextField} from "@material-ui/core";
import styled from "styled-components";
import {ButtonWidget} from "../../../widget/base/button";
import {ButtonStyle as InputButton, FlexStyle, InputStyle} from "../../../widget/base/input";
import {getBalanceFormat, getBalanceNumber, getMaxNumber} from "../../../utils/formatBalance";
import {SendModal} from "../../../widget/modal/send_modal";
import BigNumber from "bignumber.js";
import {useWithdraw} from "../../../server/stake";
import {ModalContext} from "../../../provider/model_provider";
import {useBalance} from "../../../utils/token_server";
import {useWeb3} from "../../../provider/web3_provider";
import {useWallet} from "use-wallet";

export const DepositedInput: React.FC = () => {
    const {contrcts} = useWeb3();
    const {account} = useWallet();
    const [_number, setNumber] = useState<any>();
    const {withdrawAll, withdraw} = useWithdraw();
    const {onPresent} = useContext(ModalContext);

    const balance = useBalance(contrcts.stakeContract, account ?? '');


    let _max = useMemo(() => {
        return getBalanceNumber(balance);
    }, [balance]);

    const handleWithdraw = useCallback(async () => {
        if (_number == _max) {
            onPresent(<SendModal confirmationFn={() => withdrawAll()}/>);
        } else {
            onPresent(<SendModal confirmationFn={() => withdraw(getMaxNumber(_number, new BigNumber(balance)))}/>);
        }
    }, [balance, withdrawAll, _number, withdraw]);
    return (
        <div>
            <ValueStyle>
                <h3>Unstake: </h3>
                <Grid container alignItems={"center"} spacing={4}>
                    <Grid item md={9} sm={12} xs={12}>
                        <FlexStyle className={'flex_style'}>
                            <InputStyle>
                                <TextField
                                    value={_number}
                                    fullWidth={true}
                                    placeholder={`Amount to withdraw`}
                                    onChange={(ev) => {
                                        setNumber(ev.target.value);
                                    }}
                                />

                            </InputStyle>

                            <div className={'balance'}>Balance: <span>{getBalanceFormat(balance, 2)}</span></div>

                            <InputButton onClick={() => {
                                setNumber(_max);
                            }}>Max</InputButton>
                        </FlexStyle>
                    </Grid>

                    <Grid item md={3} xs={12}>
                        <ButtonWidget onClick={handleWithdraw} isDisable={balance.toFixed() === '0' || !_number}
                                      text={'Withdraw'}/>
                    </Grid>
                </Grid>


            </ValueStyle>
        </div>
    );
}


const ValueStyle = styled.div`
  padding: 20px 30px;
  box-sizing: border-box;

  .ButtonWidget {
    height: 40px;
  }


  @media (max-width: ${({theme}) => theme.sm}) {
    padding: 10px 20px;
    h3 {
      span {
        display: block;
        font-size: 14px;
        color: #51516E;
      }
    }

    .flex_style {
      margin: 15px 0 0 0;
    }
  }


  h3 {
    font-size: 18px;

    font-weight: 600;
    color: #FEFEFE;

    line-height: 22px;
  }




`
