import {useWeb3} from "../provider/web3_provider";
import {useSendFn} from "../utils/web3_send";
import {useCallback, useEffect, useState} from "react";
import BigNumber from "bignumber.js";
import {Contract} from "web3-eth-contract";


export const useDeposit = (sVaultContract: Contract | undefined, sVaultAddress: string | undefined) => {
    const {contrcts, web3} = useWeb3();
    const handleSendFn = useSendFn(sVaultAddress);
    const depositAll = useCallback(async () => {
        if (!sVaultContract) return;
        let method = sVaultContract.methods.depositAll();
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3
    ])

    const deposit = useCallback(async (amount: string) => {
        if (!sVaultContract) return;
        let method = sVaultContract.methods.deposit(amount);
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3

    ]);

    return {depositAll, deposit}
}

export const useWithdraw = (sVaultContract: Contract | undefined, sVaultAddress: string | undefined) => {
    const {contrcts, web3} = useWeb3();
    const handleSendFn = useSendFn(sVaultAddress);
    const withdrawAll = useCallback(async () => {
        if (!sVaultContract) return;
        let method = sVaultContract.methods.withdrawAll();
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3
    ])

    const withdraw = useCallback(async (amount: string) => {
        if (!sVaultContract) return;
        let method = sVaultContract.methods.withdraw(amount);
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3

    ]);

    return {withdraw, withdrawAll}
}

export const useGetPricePerFullShare = (sVaultContract: Contract | undefined): BigNumber => {
    const {contrcts, web3, blockNumber} = useWeb3();
    const [share, setShare] = useState(new BigNumber(0));
    const getPricePerFullShare = useCallback(async () => {
        if (sVaultContract == null) return;
        try {
            const _value = await sVaultContract.methods.getPricePerFullShare().call();
            setShare(new BigNumber(_value));
        } catch (e) {
            console.log(e, '<--------err-------useGetPricePerFullShare');
        }
    }, [contrcts]);

    useEffect(() => {
        getPricePerFullShare()
    }, [blockNumber, sVaultContract])

    return share;
}

export const useAnnualRewardPerShare = (sVaultContract: Contract | undefined): BigNumber => {
    const {contrcts, web3, blockNumber} = useWeb3();
    const [share, setShare] = useState(new BigNumber(0));
    const getAnnualRewardPerShare = useCallback(async () => {
        if (sVaultContract == null) return;
        try {
            const _value = await sVaultContract.methods.annualRewardPerShare().call();
            setShare(new BigNumber(_value).times(100));
        } catch (e) {
            console.log(e, '<--------err-------useAnnualRewardPerShare');
        }
    }, [contrcts]);

    useEffect(() => {
        getAnnualRewardPerShare()
    }, [blockNumber, sVaultContract])

    return share;
}

