import React, {useCallback, useContext, useMemo, useState} from "react";
import {Grid, Slider, TextField, Typography} from "@material-ui/core";
import styled from "styled-components";
import {ButtonWidget} from "../../../widget/base/button";
import {ApproveButton} from "../../../widget/base/approve_button";
import {useWeb3} from "../../../provider/web3_provider";
import {ButtonStyle as InputButton, FlexStyle, InputStyle} from "../../../widget/base/input";
import {getBalanceFormat, getBalanceNumber, getMaxNumber} from "../../../utils/formatBalance";
import {useDeposit} from "../../../server/stake";
import BigNumber from "bignumber.js";
import {SendModal} from "../../../widget/modal/send_modal";
import {ModalContext} from "../../../provider/model_provider";
import {useSunderBarContext} from "../../../provider/sunder_bar_provider";

export const ApproverInput: React.FC = () => {

    const {contrcts} = useWeb3();

    const {sunderBalance} = useSunderBarContext();

    const [_number, setNumber] = useState<any>();

    const {depositAll, deposit} = useDeposit();

    const {onPresent} = useContext(ModalContext);

    let _max = useMemo(() => {

        return getBalanceNumber(sunderBalance);

    }, [sunderBalance]);

    const handleDeposit = useCallback(async () => {
        if (_number == _max) {
            onPresent(<SendModal confirmationFn={() => depositAll()}/>);
        } else {
            onPresent(<SendModal confirmationFn={() => deposit(getMaxNumber(_number, new BigNumber(sunderBalance)))}/>);
        }
    }, [sunderBalance, depositAll, _number, deposit]);
    return (
        <div>
            <ValueStyle>
                <h3>Stake Sunder:</h3>
                <Grid container alignItems={"center"} spacing={4}>
                    <Grid item md={9} xs={12}>
                        <FlexStyle className={'flex_style'}>
                            <InputStyle>
                                <TextField
                                    value={_number}
                                    fullWidth={true}
                                    placeholder={`Amount to deposit`}
                                    onChange={(ev) => {
                                        setNumber(ev.target.value);
                                    }}
                                />
                            </InputStyle>

                            <div className={'balance'}>Balance: <span>{getBalanceFormat(sunderBalance, 2)}</span></div>

                            <InputButton onClick={() => {
                                setNumber(_max);
                            }}>Max</InputButton>
                        </FlexStyle>
                    </Grid>

                    <Grid item md={3} xs={12}>
                        <ApproveButton contract={contrcts.sunderTokenContract}
                                       contractAddress={contrcts.sunderTokenAddress}
                                       spender={contrcts.stakeAddress}>
                            <ButtonWidget isDisable={sunderBalance.toFixed() === '0' || !_number} onClick={handleDeposit}
                                          text={'Deposit'}/>
                        </ApproveButton>
                    </Grid>
                </Grid>


            </ValueStyle>
        </div>
    );
}


const ValueStyle = styled.div`
  padding: 20px 30px;
  box-sizing: border-box;

  @media (max-width: ${({theme}) => theme.sm}) {
    padding: 10px 20px;
    h3 {
      span {
        display: block;
        font-size: 14px;
        color: #FEFEFE;
      }
    }

    .flex_style {
      margin: 15px 0 0 0;
    }
  }

  .ButtonWidget {
    height: 40px;
  }

  h3 {
    font-size: 18px;

    font-weight: 600;
    color: #FEFEFE;
    line-height: 22px;
  }


`
