import React, {createContext, useContext} from "react";
import {Contract} from "web3-eth-contract";
import BigNumber from "bignumber.js";


export const SunderBarContext = createContext<SunderBarInterface>({
    // deposited: new BigNumber(0),
    balance: new BigNumber(0),
    sunderBalance: new BigNumber(0),
    // priceShare: new BigNumber(0),
});

export interface SunderBarInterface {
    // deposited: BigNumber,
    balance: BigNumber,
    sunderBalance: BigNumber,
    // priceShare: BigNumber,
}


export const useSunderBarContext = () => {
    let data = useContext(SunderBarContext);
    return data;
}
